export default ({navigation}) => {
  if (navigation.length) {
    return (
      <div className="navigation">
        {navigation.map((nav, index) => {
          return (
            <a key={index} className={`nav-item ${nav.tagName.toLowerCase()}`} href={`#${index}`}>
              {nav.innerText}
            </a>
          );
        })}
      </div>
    );
  } else {
    return [];
  }
};
